


































































import Vue from "vue";

export default Vue.extend({
  name: "Appbar",
  data: () => ({
    title: "OpenBaas",
    loading: false,
    items: [] as string[],
    search: null,
    select: null,
    states: ["Alabama"],
    isSearching: false,
    isFocused: false
  }),
  watch: {
    search(val) {
      val && val !== this.select && this.querySelections(val);
    }
  },
  methods: {
    querySelections(v: string) {
      this.loading = true;
      // Simulated ajax query
      setTimeout(() => {
        this.items = this.states.filter(e => {
          return (e || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1;
        }) as string[];
        this.loading = false;
      }, 500);
    },
    onClickUser() {
      this.$router.push("login");
    },
    onFocus() {
      this.isFocused = true;
    },
    onBlur() {
      this.isFocused = false;
    }
  }
});
